<template>
  <div class="blog-detail-container">
    <div class="blog-detail-content animate__animated animate__fadeIn">
      <div class="blog-meta">
        <span class="blog-date">{{ formatDate(article.date) }}</span>
        <span class="blog-category">{{ getCategoryName(article.category) }}</span>
        <span class="blog-views">{{ article.views }} 阅读</span>
      </div>

      <h1 class="blog-title">{{ article.title }}</h1>
      
      <div class="blog-tags">
        <el-tag 
          v-for="tag in article.tags" 
          :key="tag"
          effect="plain"
          size="small"
        >
          {{ tag }}
        </el-tag>
      </div>

      <div class="blog-content" v-html="article.content"></div>

      <div class="blog-author">
        <div class="author-avatar">
          <img src="../assets/avatar.jpg" alt="作者头像">
        </div>
        <div class="author-info">
          <h3>关于作者</h3>
          <p>资深Java开发工程师，拥有多年微服务架构和性能优化经验。</p>
        </div>
      </div>

      <div class="blog-comments">
        <h2>评论 ({{ article.comments.length }})</h2>
        
        <div class="comment-form">
          <el-input
            v-model="newComment"
            type="textarea"
            :rows="3"
            placeholder="写下你的评论..."
          />
          <el-button type="primary" @click="submitComment">发表评论</el-button>
        </div>

        <div class="comments-list">
          <div 
            v-for="comment in article.comments" 
            :key="comment.id"
            class="comment-item"
          >
            <div class="comment-avatar">
              <img :src="comment.avatar" :alt="comment.author">
            </div>
            <div class="comment-content">
              <div class="comment-header">
                <span class="comment-author">{{ comment.author }}</span>
                <span class="comment-date">{{ formatDate(comment.date) }}</span>
              </div>
              <p class="comment-text">{{ comment.content }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup name="BlogDetailPage">
import { ref, onMounted } from 'vue';
import { useRoute } from 'vue-router';

const route = useRoute();
const newComment = ref('');

// 模拟文章数据
const article = ref({
  id: 1,
  title: 'Spring Boot 最佳实践指南',
  content: `
    <h2>引言</h2>
    <p>Spring Boot已经成为Java开发中最流行的框架之一，它极大地简化了Spring应用的开发流程。本文将分享一些在实际项目中总结的Spring Boot最佳实践。</p>

    <h2>1. 项目结构规范</h2>
    <p>良好的项目结构是保证代码可维护性的基础。建议按照以下方式组织代码：</p>
    <ul>
      <li>controller：处理HTTP请求</li>
      <li>service：业务逻辑层</li>
      <li>repository：数据访问层</li>
      <li>model：数据模型</li>
      <li>config：配置类</li>
      <li>util：工具类</li>
    </ul>

    <h2>2. 配置管理</h2>
    <p>合理的配置管理可以提高应用的灵活性：</p>
    <ul>
      <li>使用YAML代替properties文件</li>
      <li>利用profile进行环境隔离</li>
      <li>敏感信息使用加密配置</li>
    </ul>

    <h2>3. 依赖注入最佳实践</h2>
    <p>依赖注入是Spring的核心特性，建议：</p>
    <ul>
      <li>优先使用构造器注入</li>
      <li>使用@Autowired注解时指定required属性</li>
      <li>合理使用作用域注解</li>
    </ul>
  `,
  date: '2023-05-15',
  category: 1,
  tags: ['Spring Boot', 'Java'],
  views: 1250,
  comments: [
    {
      id: 1,
      author: '张三',
      avatar: 'https://avatars.githubusercontent.com/u/1?v=4',
      content: '文章写得很详细，对项目结构的建议特别有帮助！',
      date: '2023-05-16'
    },
    {
      id: 2,
      author: '李四',
      avatar: 'https://avatars.githubusercontent.com/u/2?v=4',
      content: '配置管理这部分讲得很好，学到了很多。',
      date: '2023-05-17'
    }
  ]
});

// 格式化日期
const formatDate = (dateString) => {
  return new Date(dateString).toLocaleDateString('zh-CN');
};

// 获取分类名称
const getCategoryName = (categoryId) => {
  const categories = {
    1: 'Java开发',
    2: '系统架构',
    3: '性能优化',
    4: '微服务'
  };
  return categories[categoryId] || '';
};

// 提交评论
const submitComment = () => {
  if (!newComment.value.trim()) {
    return;
  }

  const comment = {
    id: article.value.comments.length + 1,
    author: '访客用户',
    avatar: 'https://avatars.githubusercontent.com/u/3?v=4',
    content: newComment.value,
    date: new Date().toISOString().split('T')[0]
  };

  article.value.comments.unshift(comment);
  newComment.value = '';
};

// 在组件挂载时更新浏览量
onMounted(() => {
  // 这里可以调用API更新浏览量
  article.value.views++;
});
</script>

